@import "https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css";


.App-header {
  text-align: center;
  background-color: #ddd;
  margin: 0;
  top: 0;
}

.App-title-box {
  vertical-align: middle;
  padding: 0;
}

.App-title {
  text-align: center;
  font: bold 40px Arial, sans-serif;
}

.App-logo {
  height: 62px;
  width: 62px;
  position: absolute;
  top: 0;
  left: 0;
}

.buzzy-indicator {
  text-align: center;
  z-index: 2;
  text-shadow: 0 0 15px #add8e6;
  position: fixed;
  top: calc(50vh - 35px);
  left: calc(50vw - 170px);
}

.buzzy-indicator > div {
  z-index: 3;
  background-color: #dcf0f0;
  border-radius: 4px;
  padding: 4px;
}

.icon-spin {
  animation: icon-spin 2s linear infinite;
}

@keyframes icon-spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(359deg);
  }
}

#tools {
  text-align: left;
  background-color: #e7f9e7;
  padding-left: 30px;
  position: sticky;
  top: 0;
}

