  --text-color-xlight: #fff;
  --bg-color: #d8d8d8;
  --bg-color-light: #f3f3f3;
  --border-color: #949494;
  --overlay-color: #000000bf;
  --box-shadow-color: #1414141f;
  --state-info: var(--theme1-400);
  --state-success: var(--success-600);
  --state-warning: var(--warning-400);
  --state-danger: var(--danger-500);
  --state-danger-light: var(--danger-400);
  --state-disabled: #6f6f6f;
  --spacer: 1.5rem;
  --spacer-3xl: calc(var(--spacer)  * 4);
  --spacer-xxl: calc(var(--spacer)  * 2.5);
  --spacer-xl: calc(var(--spacer)  * 2);
  --spacer-l: calc(var(--spacer)  * 1.5);
  --spacer-s: calc(var(--spacer)  / 1.5);
  --spacer-xs: calc(var(--spacer)  / 2);
  --spacer-xxs: calc(var(--spacer)  / 3);
  --spacer-3xs: calc(var(--spacer)  / 6);
  --screen-xs: 30rem;
  --screen-s: 45rem;
  --screen-l: 62rem;
  --screen-xl: 75rem;
  --screen-xxl: calc(var(--screen-xl)  + calc(var(--spacer-3xl)  * 2)  + calc(var(--spacer)  * 2) );
  --screen-xs-max: 44.9375rem;
  --screen-s-max: 61.9375rem;
  --screen-l-max: 74.9375rem;
  --screen-xl-max: 89.9375rem;
  --font-family-base: "Sun Antwerpen", sans-serif;
  --font-family-headings: "Antwerpen Regular", sans-serif;
  --font-family-monospace: "Menlo", "Monaco", "Consolas", "Courier New", monospace;
  --font-weight-light: 300;
  --font-weight-normal: 500;
  --font-weight-bold: 700;
  --font-weight-extra-bold: 800;
  --font-weight-h1: var(--font-weight-normal);
  --font-weight-h2: var(--font-weight-normal);
  --font-weight-h3: var(--font-weight-normal);
  --font-weight-h4: var(--font-weight-normal);
  --font-weight-h5: var(--font-weight-normal);
  --font-weight-h6: var(--font-weight-normal);
  --link-color: var(--theme1-400);
  --link-hover-color: var(--theme1-500);
  --border-radius: 0;
  --border-width: 1px;
  --animation-duration: 200ms;
  --animation-easing: ease-in-out;
  --animation-normal: var(--animation-duration) var(--animation-easing);
  --box-shadow-bottom-left: calc(var(--spacer)  / -2) calc(var(--spacer)  / 2) 0 var(--box-shadow-color);
  --box-shadow-bottom-right: calc(var(--spacer)  / 2) calc(var(--spacer)  / 2) 0 var(--box-shadow-color);
  --box-shadow-upper-left: calc(var(--spacer)  / -2) calc(var(--spacer)  / -2) 0 var(--box-shadow-color);
  --box-shadow-upper-right: calc(var(--spacer)  / 2) calc(var(--spacer)  / -2) 0 var(--box-shadow-color);
  --box-shadow: var(--box-shadow-bottom-right);
  --shared-avatar-size-s: var(--spacer-l);
  --shared-avatar-size: var(--spacer-xl);
  --shared-avatar-size-l: var(--spacer-xxl);
  --shared-copyright-color: #fff;
  --shared-copyright-height: var(--spacer-l);
  --shared-header-height: var(--spacer-xl);
  --shared-slideshow-footer-color-light: #fff;
