.ai {
  vertical-align: middle;
  justify-content: center;
  align-self: center;
  line-height: 1;
  display: inline-flex;
}

.ai svg {
  fill: none;
  height: 1em;
  stroke: currentColor;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-width: 6.25%;
  width: 1em;
  position: relative;
}

.ai--thin svg {
  stroke-width: 4.6875%;
}

.ai--spin svg {
  animation: spin 1.6s linear infinite reverse;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

