@media screen and (min-width: 45rem) {
  .o-statusbar__notification {
    padding: var(--spacer-xs) var(--spacer-l);
  }
}

@media screen and (min-width: 62rem) {
  .o-statusbar__notification {
    padding: var(--spacer-xs) var(--spacer-xl);
  }
}

@media screen and (min-width: 75rem) {
  .o-statusbar__notification {
    padding: var(--spacer-xs) var(--spacer-3xl);
  }
}

.o-statusbar__notification p {
  margin: 0;
}

.o-statusbar__notification a {
  color: #fff;
}

.o-statusbar .a-button {
  background: rgba(0, 0, 0, 0.2);
  height: auto;
  margin: 0;
}

.o-statusbar .a-button .ai {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: center;
  line-height: initial;
}

/**
 * AUI TABLE
 * -------------------------------------------------------------------
 * - Column selector
 * - Table bar
 */

/**
 * COLUMN SELECTOR
 * -------------------------------------------------------------------
 */

.a-table-column-selector {
  margin: 0 0 -1px;
  max-height: 70vh;
  overflow: auto;
}

.a-table-column-selector__actions {
  display: flex;
  flex-direction: column;
  margin-left: var(--spacer-3xs);
  margin-right: 0;
}

.a-table-column-selector > li {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 0;
}

.a-table-column-selector > li:first-child {
  border-top: none;
}

.a-table-column-selector > li:last-child {
  border-bottom: none;
}

.a-table-column-selector > li.show {
  -webkit-animation: fadeIn var(--animation-normal);
  animation: fadeIn var(--animation-normal);
}

.a-table-column-selector > li.active {
  background-color: #f3f3f3;
}

/**
 * TABLE BAR
 * -------------------------------------------------------------------
 */

.a-table-bar {
  background-color: #d8d8d8;
  display: flex;
  padding: var(--spacer-xs);
}

.a-table-bar__filters {
  flex: 1;
  margin-right: var(--spacer);
  min-height: var(--spacer-xxl);
  overflow: hidden;
}

.a-table-bar__filters-show-more {
  margin: var(--spacer-3xs);
}

.a-table-bar__filters--open {
  height: 100%;
}

.a-table-bar__item {
  display: inline-block;
  margin: var(--spacer-3xs);
}

.a-table-bar__search {
  background: #fff;
  height: 100%;
  margin: var(--spacer-3xs);
}

/**
 * AUI UPLOAD
 * -------------------------------------------------------------------
 */

.m-upload__button {
  position: relative;
}